import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import {
  Navbar,
  NavbarGroup,
  NavbarHeading,
  NavbarDivider,
  Button,
  Alignment
} from "@blueprintjs/core";
import { Grid, Row, Col } from "react-flexbox-grid";
import "@blueprintjs/core/lib/css/blueprint.css";
import "@blueprintjs/icons/lib/css/blueprint-icons.css";
import "react-virtualized/styles.css";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import City from "./city/City";

const linkTypes = {
  CITY: "city",
  STATE: "state",
  FEDERAL: "federal"
};

const links = [
  {
    url: "https://en.wikipedia.org/wiki/Burien,_Washington",
    title: "Burien Wikipedia Page",
    description: "Wikipedia page with useful information",
    type: linkTypes.CITY
  },
  {
    url:
      "https://burienwa.gov/UserFiles/Servers/Server_11045935/Image/City%20Hall/City%20Council/City_Council_hero_SIZED.jpg",
    title: "Burien City Council Photo",
    description: "Photo of the Burien city council members.",
    type: linkTypes.CITY
  },
  {
    url: "https://burienwa.gov/city_hall/city_council",
    title: "Burien City Council Website",
    description:
      "This is the burienwa.gov city councile webpage. Go here to find out information on the city council and how to contact any of the members.",
    type: linkTypes.CITY
  },
  {
    url: "https://en.wikipedia.org/wiki/Council%E2%80%93manager_government",
    title: "Government and infrastructure",
    description:
      "This wikipedia page explains the type of city government in Burien Burien is a Council-Manager government. There are seven elected councilmembers. A mayor is chosen by the council from among the elected councilmembers. The City Manager is appointed by the council, and reports to the entire council.",
    type: linkTypes.CITY
  },
  {
    url: "https://en.wikipedia.org/wiki/King_County_Sheriff%27s_Office",
    title: "King County Sheriff Wiki",
    description:
      "From wiki: Burien contracts with the King County Sheriff's Office for police services. Deputies assigned to Burien wear city uniforms and drive patrol cars marked with the city logo. According to the Chief of Police, there are currently 43 patrol officers, detectives, support personnel and a chief assigned full-time to the city.",
    type: linkTypes.CITY
  },
  {
    url: "https://burienwa.gov/",
    title: "City of Burien",
    description: "Website for City of Burien",
    type: linkTypes.CITY
  },
  {
    url: "https://www.codepublishing.com/WA/Burien/",
    title: "Burien Municipal Code",
    description: "Here is where you will find all the relevant municiple code",
    type: linkTypes.CITY
  },
  // {
  //   url: "",
  //   title: "",
  //   description: "",
  //   type: linkTypes.CITY
  // },
  // {
  //   url: "",
  //   title: "",
  //   description: "",
  //   type: linkTypes.CITY
  // },
  // {
  //   url: "",
  //   title: "",
  //   description: "",
  //   type: linkTypes.CITY
  // },
  // {
  //   url: "",
  //   title: "",
  //   description: "",
  //   type: linkTypes.CITY
  // },
  {
    url: "https://en.wikipedia.org/wiki/Burien,_Washington",
    title: "Burien Wikipedia Page",
    description: "",
    type: linkTypes.STATE
  }
];

const cityLinks = links.filter(l => l.type === linkTypes.CITY);

const Home = () => (
  <div>
    <h2>Welcome to Burien.io</h2>
    <p>
      This site is designed to help people in Burien locate solutions to
      problems by providing them with access to information.
    </p>
  </div>
);

const CityWrap = () => <City links={cityLinks} />;

const About = () => (
  <div>
    <h2>About</h2>
  </div>
);

const Topics = ({ match }) => (
  <div>
    <h3>Topics</h3>
    {/* {match.params.topicId} */}
  </div>
);

class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
          <Navbar>
            <NavbarGroup align={Alignment.LEFT}>
              <Link to="/">
                <Button className="pt-minimal" icon="home" text="Burien.io" />
              </Link>
              {/* <Link to="/city">
                <Button className="pt-minimal" icon="document" text="City" />
              </Link>
              <Link to="/federal">
                <Button className="pt-minimal" icon="document" text="Federal" />
              </Link>
              <Link to="/state">
                <Button className="pt-minimal" icon="document" text="State" />
              </Link> */}
            </NavbarGroup>
          </Navbar>
          <Grid fluid>
            <Row center="xs">
              <Col xs={11} md={10} className="burien-container">
                <Route exact path="/" component={Home} />
                <Route path="/" component={CityWrap} />
                <Route path="/state" component={Topics} />
                <Route path="/federal" component={Topics} />
              </Col>
            </Row>
          </Grid>
        </div>
      </Router>
    );
  }
}

export default App;



