import React, { Component } from "react";
import city_council_photos from "../city_council_photos.jpg";
import { Tooltip, Position } from "@blueprintjs/core";
import { Table, Column } from "react-virtualized";

const cityCouncilUrl = "https://burienwa.gov/city_hall/city_council";
const cityCouncilPhoto =
  "https://burienwa.gov/UserFiles/Servers/Server_11045935/Image/City%20Hall/City%20Council/City_Council_hero_SIZED.jpg";

export default class City extends Component {
  render() {
    const { links = [] } = this.props;
    console.log(`Total Links:${links.length}`, links);

    const renderLinkCell = ({ cellData, rowData }) => (
      <a href={cellData} target="_blank">
        {rowData.title}
      </a>
    );

    const tableWidth = 500;
    const tableHeight = links.length * 40;

    return (
      <div>
        <hr />
        <h3>Important Burien City links</h3>
        <Table
          width={tableWidth}
          height={tableHeight}
          headerHeight={20}
          rowHeight={30}
          rowCount={links.length}
          rowGetter={({ index }) => links[index]}
        >
          <Column
            label="Link"
            dataKey="url"
            cellRenderer={renderLinkCell}
            width={tableWidth * 0.5}
          />
          <Column
            label="Notes"
            dataKey="description"
            width={tableWidth * 0.5}
          />
        </Table>
        <div>
          <img
            src={city_council_photos}
            style={{ maxWidth: "500px" }}
            alt="Italian Trulli"
          />
          <br />
          <span>
            <Tooltip
              content={
                "https://burienwa.gov/UserFiles/Servers/Server_11045935/Image/City%20Hall/City%20Council/City_Council_hero_SIZED.jpg"
              }
            >
              <i>
                Pulled 6/3/2018 from{" "}
                <a href={cityCouncilPhoto} target="_blank">
                  https://burienwa.gov/
                </a>
              </i>
            </Tooltip>
          </span>
        </div>
      </div>
    );
  }
}



